body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  background-color: #659d9e;
}

.container {
  max-width: 90%;
  padding: 3% 0;
}

h1 {
  font-size: 2em;
}

h2 {
  font-size: 1.5em;
}

h3 {
  font-size: 1.25em;
}

h4 {
  font-size: 1em;
}

p {
  font-size: 1rem;
}

textarea {
  width: 100%;
  height: 500px;
  resize: none;
  box-sizing: border-box;
}

img {
  clear: left;
  width: 25%;
  margin: 0 auto;
  display: block;
}

pre,
code {
  background-color: #eeeeee;
}

#preview,
#editor {
  border: 1px solid black;
  padding: 2%;
  background-color: #fff;
}

h1.title {
  border: 1px solid black;
  padding: 13px;
  margin-bottom: 0px;
  background-color: #fff;
  font-weight: bold;
}

@media only screen and (max-width: 1100px) {
  textarea {
    height: 40vh;
  }

  .col-sm-5,
  .col-sm-7 {
    -webkit-flex: 0 0 100%;
            flex: 0 0 100%;
    max-width: 100%;
  }
}

